<template>
  <v-card outlined :width="getSize()" class="transparent">
    <v-card-title class="justify-center text-center white--text meticula">
      <div>Fill Your Information</div>

      <div>& We Will Contact You</div>
    </v-card-title>
    <form
      name="Get-Info"
      method="post"
      netlify
      data-netlify="true"
      data-netlify-honeypot="bot-field"
    >
      <input type="hidden" name="form-name" value="Get-Info" />
      <v-text-field
        v-model="name"
        :error-messages="nameErrors"
        :counter="10"
        label="Name"
        name="name"
        class="contentborder"
        required
        @input="$v.name.$touch()"
        @blur="$v.name.$touch()"
      ></v-text-field>
      <v-text-field
        v-model="phone"
        :error-messages="phoneErrors"
        :counter="10"
        label="Phone Number"
        name="phone"
        class="contentborder"
        required
        @input="$v.name.$touch()"
        @blur="$v.name.$touch()"
      ></v-text-field>
      <v-text-field
        v-model="email"
        :error-messages="emailErrors"
        label="E-mail"
        name="e-mail"
        class="contentborder"
        required
        @input="$v.email.$touch()"
        @blur="$v.email.$touch()"
      ></v-text-field>
      <div>
        <br />
        <br />
        <v-btn class="white cyan--text center1" @click="submit">submit</v-btn>
      </div>
    </form>
  </v-card>
</template>

<script>
import { validationMixin } from "vuelidate";
import {
  required,
  maxLength,
  email,
  minLength,
  numeric,
} from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(10) },
    phone: {
      required,
      numeric,
      maxLength: maxLength(10),
      minLength: minLength(7),
    },
    email: { required, email },
    select: { required },
    checkbox: {
      checked(val) {
        return val;
      },
    },
  },

  data: () => ({
    name: "",
    email: "",
    phone: "",
    select: null,
    action: "",
    items: ["One-Page Website", "Resume Website", "Full Website", "Other"],
    checkbox: false,
    submited: { name: "", email: "", select: "" },
    windowWidth: window.innerWidth,
  }),

  computed: {
    checkboxErrors() {
      const errors = [];
      if (!this.$v.checkbox.$dirty) return errors;
      !this.$v.checkbox.checked && errors.push("You must agree to continue!");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("Name must be at most 10 characters long");
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Must be valid e-mail");
      !this.$v.email.required && errors.push("E-mail is required");
      return errors;
    },
    phoneErrors() {
      const errors = [];
      if (!this.$v.phone.$dirty) return errors;
      !this.$v.phone.numeric && errors.push("Must not contain Letters");
      !this.$v.phone.minLength &&
        errors.push("Phone Must be at least 7 numbers long");
      !this.$v.phone.maxLength &&
        errors.push("Phone must be at most 10 characters long");
      !this.$v.phone.required && errors.push("Phone is required.");
      return errors;
    },
  },

  methods: {
    submit() {
      this.$v.$touch();
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.phone = "";
      this.checkbox = false;
    },
    clear() {
      this.$v.$reset();
      this.name = "";
      this.email = "";
      this.checkbox = false;
    },
    getSize() {
      if (this.windowWidth < 1200) {
        return "";
      } else {
        return "800";
      }
    },
  },
};
</script>
<style scoped>
@font-face {
  font-family: "meticula";
  src: url(../assets/Meticula-Regular.ttf);
  font-style: normal;
  font-weight: 100;
}
.meticula {
  font-family: meticula !important;
  font-size: calc(15px + 0.5vw);
}
.center1 {
  width: 40%;
  left: 30%;
}

.contentborder {
  border-style: none none none none;
  border-width: 1px;
}
</style>
